'use client';
import { useEffect } from 'react';
import { useAppDispatch, useAppSelector } from './hooks';
import { fetchData } from '../features/data/dataSlice';
import { Big_Shoulders_Display } from 'next/font/google';

const bigShouldersDisplay = Big_Shoulders_Display({
  variable: "--font-big-shoulders-display",
  subsets: ["latin"],
  weight: ["400", "500", "700"],
});


export default function Home() {
  const dispatch = useAppDispatch();
  const { projects } = useAppSelector((state) => state.data);

  useEffect(() => {
    dispatch(fetchData());
  }, [dispatch]);
  console.log(projects)
  return (
    <div className="
      flex flex-wrap w-full pr-[25px]
      md:pr-0 
    ">
      {projects.map((item) => (
        <div key={item.years} className="
        flex flex-nowrap relative w-full 
        ">
          <div className={`
            ${bigShouldersDisplay.variable} font-bigShoulders
            sticky flex flex-none items-center z-10 w-[45px] h-[185px] pt-[40px] pb-[20px] text-[24px]
            [writing-mode:vertical-lr] [text-orientation:upright]
          `}>{item.years}</div>
        </div>
      ))}
    </div>
  );
};

